import React from "react";
import CountUp from "react-countup";
function Analytics(props) {
  const data = [
    { label: "Work Experience", count: "1.5", suffix: "Years" },
    { label: "Projects Worked", count: "10" },
    { label: "Lines Of Code", count: "1267932" },
  ];
  return (
    <div className="analytics">
      <div className="analytics__container">
        {data.map((val) => (
          <div className="analytics__container__card">
            <div className="info">
              <div className="count">
                <CountUp
                  start={0}
                  end={val.count}
                  duration={2}
                  separator=" "
                  suffix={val.suffix ? " " + val.suffix : "+"}
                ></CountUp>
                {/* {val.suffix} */}
              </div>
              <div className="name">{val.label}</div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Analytics;
